<template>
  <Nav />
  <div v-if="!isLoading">
    <div class="page" v-if="!auth">
      <h1>You are not authorised to view this page</h1>
    </div>

    <div class="page" v-if="auth">
      <p><i>You may click on the header to sort the table</i></p>

      <div style="overflow-x: auto">
        <table class="table">
          <thead>
            <tr class="tr">
              <th class="th">Collected?</th>
              <th class="th" @click="sort('address')">Postal Code</th>
              <th class="th" @click="sort('unit')">Unit Number</th>
              <th class="th" @click="sort('name')">Name</th>
              <th class="th" @click="sort('contact')">Contact No.</th>
              <th class="th" :key="item" v-for="item in items">{{ item }}</th>
            </tr>
          </thead>
          <tbody>
            <tr class="tr" :key="donor.id" v-for="donor in donors">
              <td class="td">
                <input type="checkbox" v-model="collected" />
              </td>
              <td class="td">{{ donor.address }}</td>
              <td class="td">{{ donor.unit }}</td>
              <td class="td">{{ donor.name }}</td>
              <td class="td">{{ donor.contact }}</td>

              <td class="td" :key="item.id" v-for="item in donor.items">
                <p v-if="item.quantity != 0">{{ item.quantity }}</p>
                <p v-else>0</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <button class="back" @click="$router.push(`/mycampaigns/${campaignid}`)">
        Back to My Campaigns
      </button>
    </div>
  </div>
</template>

<script>
import VueJwtDecode from "vue-jwt-decode";
import Nav from "../components/Nav.vue";
import axios from "axios";

export default {
  name: "DonorsInfo",

  data() {
    return {
      user: {},
      auth: false,
      campaignid: "",
      campaign: [],
      items: [],
      donors: [],
      currentSort: "address",
      currentSortDir: "asc",
      isLoading: true,
      // itemCount: [],
      halal: false,
    };
  },

  components: {
    Nav,
  },

  methods: {
    getUserDetails() {
      let token = localStorage.getItem("jwt");
      if (token) {
        let decoded = VueJwtDecode.decode(token);
        this.user = decoded;
      }
    },

    checkAuth() {
      let admins = this.campaign.admin;
      for (var i = 0; i < admins.length; i++) {
        if (admins[i].email === this.user.email) {
          this.auth = true;
        }
      }
    },

    async getCampaign() {
      const res = await axios.post("/api/campaign/getCampaignById", {
        _id: this.campaignid,
      });

      const data = await res.data[0];

      return data;
    },

    getItems() {
      let arr = [];
      for (let i = 0; i < this.campaign.items.length; i++) {
        let item = this.campaign.items[i].item;
        arr = [...arr, item];
      }
      this.items = arr;
    },

    getCampaignId() {
      this.campaignid = localStorage.getItem("donorsInfoId");
    },

    async getDonors() {
      const res = await axios.post("/api/donate/getDonors", {
        campaignid: this.campaignid,
      });

      return res.data;
    },

    sort(field) {
      if (field === this.currentSort) {
        this.currentSortDir = this.currentSortDir === "asc" ? "desc" : "asc";
      }

      this.currentSort = field;

      this.donors.sort((a, b) => {
        let modifier = 1;
        if (this.currentSortDir === "desc") modifier = -1;
        if (a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if (a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },

    // getItemCount() {
    //     // this.getItems();
    //     //   // let arr = [];

    //     //   // for (let i = 0; i < this.items.length; i++) {
    //     //   //   arr = [...arr, 0];
    //     //   // }

    //     for (let j = 0; j < this.donors.length; j++) {
    //       let donor = this.donors[j]; // Loop about the particular donor

    //       for (let k = 0; k < donor.items.length; k++) {
    //         this.donationMade = donor.items[k].length;
    //       }
    //     }
    // },
  },

  async created() {
    this.getUserDetails();
    this.getCampaignId();
    this.campaign = await this.getCampaign();
    this.checkAuth();
    this.getItems();
    // this.getItemCount();
    this.donors = await this.getDonors();
    this.sort("address");
    this.isLoading = false;
  },
};
</script>

